.dock {
	grid-area: Dock;
	width: 100%;
	height: 100%;
	padding: 5px;
	box-sizing: border-box;
	left: 0%;
	position: relative;
	animation: slide-in-from-left 1s ease-out both;
	animation-delay: 0.5s;
}
@keyframes slide-in-from-left {
	from {
		left: -100%;
	}
	to {
		left: 0%;
	}
}

.stage {
	background-color: rgba(45, 45, 45);
	width: 100%;
	height: 100%;
	border-radius: 15px;
	padding: 8px 5px;
	box-sizing: border-box;
	overflow: visible!important;
	overflow-y: auto;
}

.column {
	display: grid;
	row-gap: 0.8vh;
}

.column > *:first-of-type .button {
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
}
.column > *:nth-child(30n) .button {
	border-bottom-left-radius: 15px;
	border-bottom-right-radius: 15px;
}

.container {
	position: relative;
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
}

.container > svg {
	color: darkslategray;
}
.container .button {
	width: 100%;
	padding: 1.2vh 0;
	font-size: 2.5vh;
}
.container .button.active {
	background: repeating-linear-gradient(
		45deg,
		rgb(33, 120, 33),
		rgb(33, 120, 33), 10px,
		rgb(33, 33, 33) 10px,
		rgb(33, 33, 33) 20px
	);
}
.name {
	position: absolute;
	left: calc(100% + 15px);
	font-size: 14px;
	white-space: nowrap;
	display: flex;
	z-index: 2;
	background-color: rgb(15, 15, 25);
	padding: 10px;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	box-shadow: 1px 1px 2px rgb(15, 15, 15);
	opacity: 0.9;
	transition: opacity 0.1s ease;
	pointer-events: none;
}
.container:not(:hover) .name {
	opacity: 0;
}