div.container {
	animation: centre-split 0.333s ease;
	width: 45%;
	min-width: 500px;
}

@keyframes centre-split {
	from {
		transform: scaleY(0)
	}
	to {
		transform: scaleY(1)
	}
}

.section {
	margin: 1.5em 0 0.5em 0;
	font-weight: bold;
}

.content div:global(.keyField) {
	background-color: transparent;
}

.content div[class^='profile_row'] > div:not(:global(.keyField)),
.content div[class^='profile_row'] > div:not(:global(.keyField)) > div {
	border-radius: 15px;
	height: max-content;
	align-self: end;
}
content form[class^='components_form'] > input {
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
	outline: none;
}

.content div[class^='profile_row'] {
	margin: 6px 0;
	column-gap: 6px;
}

.templates {
	display: grid;
	width: 100%;
	grid-template-columns: 1fr 1fr;
	margin: 10px;
	column-gap: 10px;
	row-gap: 10px;
}

.templates > div {
	height: 190px;
	background-color: rgba(40, 40, 75, 0.5);
	border-radius: 15px;
	padding: 10px;
	display: grid;
	grid-template-rows: 150px 1fr;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.templates > div > svg {
	font-size: 120px;
	justify-self: center;
}